body{
  background-color: black;
}

.App {
  text-align: center;
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
  background-color: white;
  border-radius: 10px;
}

.input_content input{
  background-color: rgba(27, 112, 137);
  border: 0;
  width: 200px;
  height: 50px;
  padding: 0 10px;
  margin: 10px;
  border-radius: 5px;
  font-size: 18px;

}

.input_content button{
  background-color: #6a197d;
  border: 0;
  width: 80px;
  height: 50px;
  border-radius: 5px;
  font-size: 18px;
  cursor: pointer;
  font-weight: bolder;
  color:#fff;
}

.input_content input::placeholder{
  color: rgba(255, 255, 255, 0.5);
}
.input_content input:focus, .input_content button:focus{
  outline: none;
}

.list{
  background-color: #000000;
  border: 0;
  width: 200px;
  height: 50px;
  padding: 0 20px;
  margin: 20px;
  border-radius: 5px;
  font-size: 18px;
}

.list p{
  padding: 10px;
  text-align: left;
  color:#fff;
  font-size: 18px;
  position: relative;
}

.list p span{
  position: absolute;
  right: 10px;
  cursor: pointer;
}

.list p span:hover{
  color:red;
}

.header{
  padding-top: 5px;
}

.header h3{
  background-color: #000000;
  border: 0;
  width: 100%;
  height: 50px;
  padding-top: 10px;
  font-size: 24px;
  color: #fff;
}






